import AppBar from '@mui/material/AppBar'
import Stack from '@mui/material/Stack'
import Toolbar from '@mui/material/Toolbar'
import Typography from '@mui/material/Typography'
import IconButton from '@mui/material/IconButton'
import { ThemeProvider, createTheme } from '@mui/material/styles'

const appBarLabel = (label: string) => (
	<Toolbar>
		<IconButton edge="start" color="inherit" aria-label="menu" sx={{ mr: 2 }}></IconButton>
		<Typography variant="h6" noWrap component="div" sx={{ flexGrow: 1 }}>
			{label}
		</Typography>
	</Toolbar>
)
const darkTheme = createTheme({ palette: { mode: 'dark', primary: { main: '#1976d2' } } })

export const Header = (props: { label: string }) => (
	<Stack spacing={2} sx={{ flexGrow: 1 }}>
		<ThemeProvider theme={darkTheme}>
			<AppBar position="static" color="primary" enableColorOnDark>
				{appBarLabel(props.label)}
			</AppBar>
		</ThemeProvider>
	</Stack>
)
